import { MODULE_ID } from '@utils/module'

export function hasModuleFlag(doc: foundry.abstract.Document) {
    return getProperty(doc, `flags.${MODULE_ID}`) !== undefined
}

export function flagPath(key: string) {
    return `flags.${MODULE_ID}.key`
}

export function getFlag<T extends unknown>(doc: foundry.abstract.Document, key: string): T | undefined
export function getFlag<T extends unknown>(doc: foundry.abstract.Document, key: string, fallback: T): T
export function getFlag<T extends unknown>(doc: foundry.abstract.Document, key: string, fallback?: T): T | undefined {
    return (doc.getFlag(MODULE_ID, key) as T | undefined) ?? fallback
}

/**
 * @template {foundry.Document} T
 * @param {T} doc
 * @param {string} key
 * @param {any} value
 * @returns T
 */
export function setFlag(doc: foundry.abstract.Document, key: string, value: unknown) {
    return doc.setFlag(MODULE_ID, key, value)
}

export function getSourceId(doc: ClientDocument) {
    return doc.getFlag<ItemUUID>('core', 'sourceId')
}

export function hasSourceId(doc: ClientDocument, sourceId: DocumentUUID) {
    return getSourceId(doc) === sourceId
}

export function includesSourceId(doc: ClientDocument, list: DocumentUUID[]) {
    const sourceId = getSourceId(doc)
    return sourceId ? list.includes(sourceId) : false
}
