import { localize } from '@utils/foundry/localize'

export type NotifyArgs = [str: string, arg1?: boolean | Record<string, string | number | boolean>, arg2?: boolean]

function notify(
    str: string,
    arg1?: 'warning' | 'info' | 'error' | boolean | Record<string, string | number | boolean>,
    arg2?: boolean | Record<string, string | number | boolean>,
    arg3?: boolean
) {
    const type = typeof arg1 === 'string' ? arg1 : 'info'
    const data = typeof arg1 === 'object' ? arg1 : typeof arg2 === 'object' ? arg2 : undefined
    const permanent = typeof arg1 === 'boolean' ? arg1 : typeof arg2 === 'boolean' ? arg2 : arg3 ?? false

    ui.notifications.notify(localize(str, data), type, { permanent })
}

export function warn(...args: NotifyArgs) {
    const [str, arg1, arg2] = args
    notify(str, 'warning', arg1, arg2)
}

export function info(...args: NotifyArgs) {
    const [str, arg1, arg2] = args
    notify(str, 'info', arg1, arg2)
}

export function error(...args: NotifyArgs) {
    const [str, arg1, arg2] = args
    notify(str, 'error', arg1, arg2)
}
