import { MODULE_ID } from '@utils/module'

export function getSettingLocalizationPath(...path: string[]) {
    return `${MODULE_ID}.settings.${path.join('.')}`
}

export function flagsUpdatePath(...path: string[]) {
    return `flags.${MODULE_ID}.${path.join('/')}`
}

export function templatePath(...path: string[]) {
    path = path.filter(x => typeof x === 'string')
    return `modules/${MODULE_ID}/templates/${path.join('/')}`
}

export function imagePath(...path: string[]) {
    return `modules/${MODULE_ID}/images/${path.join('/')}`
}
